/**
 * ××¢×¨×›×ª × ×™×˜×•×¨ ×‘×™×¦×•×¢×™× ××ª×§×“××ª
 * Advanced Performance Monitoring System
 */

export class PerformanceMonitor {
  constructor() {
    this.metrics = new Map();
    this.thresholds = new Map();
    this.alerts = [];
    this.isMonitoring = false;
  }

  /**
   * ×”×ª×—×œ×ª × ×™×˜×•×¨
   */
  startMonitoring() {
    if (this.isMonitoring) return;
    
    this.isMonitoring = true;
    this.monitorMemory();
    this.monitorCPU();
    this.monitorNetwork();
    
    console.log('ğŸ“Š × ×™×˜×•×¨ ×‘×™×¦×•×¢×™× ×”×•×¤×¢×œ');
  }

  /**
   * × ×™×˜×•×¨ ×–×™×›×¨×•×Ÿ
   */
  monitorMemory() {
    if (!this.isMonitoring) return;
    
    setInterval(() => {
      if ('memory' in performance) {
        const memory = performance.memory;
        this.metrics.set('memory', {
          used: memory.usedJSHeapSize,
          total: memory.totalJSHeapSize,
          limit: memory.jsHeapSizeLimit,
          timestamp: Date.now()
        });
        
        this.checkMemoryThresholds(memory);
      }
    }, 5000);
  }

  /**
   * × ×™×˜×•×¨ CPU
   */
  monitorCPU() {
    if (!this.isMonitoring) return;
    
    setInterval(() => {
      const startTime = performance.now();
      
      // ××“×™×“×ª ×¢×•××¡ CPU ×¤×©×•×˜×”
      setTimeout(() => {
        const endTime = performance.now();
        const cpuLoad = endTime - startTime;
        
        this.metrics.set('cpu', {
          load: cpuLoad,
          timestamp: Date.now()
        });
        
        this.checkCPUThresholds(cpuLoad);
      }, 100);
    }, 2000);
  }

  /**
   * × ×™×˜×•×¨ ×¨×©×ª
   */
  monitorNetwork() {
    if (!this.isMonitoring) return;
    
    if ('connection' in navigator) {
      const connection = navigator.connection;
      
      this.metrics.set('network', {
        effectiveType: connection.effectiveType,
        downlink: connection.downlink,
        rtt: connection.rtt,
        timestamp: Date.now()
      });
    }
  }

  /**
   * ×‘×“×™×§×ª ×¡×¤×™ ×–×™×›×¨×•×Ÿ
   */
  checkMemoryThresholds(memory) {
    const usagePercent = (memory.usedJSHeapSize / memory.jsHeapSizeLimit) * 100;
    
    if (usagePercent > 80) {
      this.createAlert('MEMORY', 'CRITICAL', `×©×™××•×© ×‘×–×™×›×¨×•×Ÿ ×’×‘×•×”: ${usagePercent.toFixed(1)}%`);
    } else if (usagePercent > 60) {
      this.createAlert('MEMORY', 'WARNING', `×©×™××•×© ×‘×–×™×›×¨×•×Ÿ ×’×‘×•×”: ${usagePercent.toFixed(1)}%`);
    }
  }

  /**
   * ×‘×“×™×§×ª ×¡×¤×™ CPU
   */
  checkCPUThresholds(cpuLoad) {
    if (cpuLoad > 100) {
      this.createAlert('CPU', 'CRITICAL', `×¢×•××¡ CPU ×’×‘×•×”: ${cpuLoad.toFixed(1)}ms`);
    } else if (cpuLoad > 50) {
      this.createAlert('CPU', 'WARNING', `×¢×•××¡ CPU ×’×‘×•×”: ${cpuLoad.toFixed(1)}ms`);
    }
  }

  /**
   * ×™×¦×™×¨×ª ×”×ª×¨××”
   */
  createAlert(type, severity, message) {
    const alert = {
      type,
      severity,
      message,
      timestamp: new Date().toISOString()
    };
    
    this.alerts.push(alert);
    console.warn(`ğŸš¨ ${type} ${severity}: ${message}`);
  }

  /**
   * ×§×‘×œ×ª ×¡×˜×˜×™×¡×˜×™×§×•×ª ×‘×™×¦×•×¢×™×
   */
  getPerformanceStats() {
    const stats = {};
    
    this.metrics.forEach((value, key) => {
      stats[key] = value;
    });
    
    return {
      metrics: stats,
      alerts: this.alerts,
      isMonitoring: this.isMonitoring
    };
  }

  /**
   * ×¢×¦×™×¨×ª × ×™×˜×•×¨
   */
  stopMonitoring() {
    this.isMonitoring = false;
    console.log('â¹ï¸ × ×™×˜×•×¨ ×‘×™×¦×•×¢×™× ×”×•×¤×¡×§');
  }
}

export const performanceMonitor = new PerformanceMonitor();
